import { Routes, RouterModule } from '@angular/router';
import { ModuleWithProviders } from '@angular/core';
import { LoanDepositChangeComponent } from './loan-deposit-change.component';
import { DepositHistoryComponent } from './deposit-history/deposit-history.component';
import { LoanHistoryComponent } from './loan-history/loan-history.component';


export const routes: Routes = [
    {
        path: '',
        component: LoanDepositChangeComponent,
    },
    {
        path: 'deposit-history',
        component: DepositHistoryComponent
    },
    {
        path: 'loan-history',
        component: LoanHistoryComponent
    }

]
export const LoanDepositChangeRouting: ModuleWithProviders = RouterModule.forChild(routes);