        let depositUrl = TZB_TABLE_DEPOSIT_LOAN + "?permissionMode=" + this.user.extParam.permissionMode + "&&extParamOrgId=" + this.user.extParam.orgId + "&&tableFlag=1&&userId=" + this.user.userId + "&&managePermission=" + this.user.extParam.managePermission + "&&operatingLevel=" + this.user.extParam.operatingLevel + "&&postId=" + this.user.extParam.postId + "";
