import { Component, OnInit, Output, EventEmitter } from '@angular/core';
import { Message } from 'primeng/primeng';
import { CALENDAR_CN } from 'app/@uisftech/common/constant/config';
import { Commfunc } from '../../../../../@uisftech/common/service/commonFun';
import { DepositHistoryBean, DepositHistoryTotalBean } from './bean/depositHistory.bean';
import { CusOperationHttpService } from '../../http/custom-operation/custom-operation.http.service';
import { checkNumber } from 'app/@uisftech/common/service/validator';
import { Validators, FormControl, FormGroup, FormBuilder } from '@angular/forms';
@Component({
    selector: 'deposit-history',
    templateUrl: './deposit-history.component.html',
    styleUrls: ['./deposit-history.component.scss'],
    providers: [Commfunc, CusOperationHttpService]
})
export class DepositHistoryComponent implements OnInit {

    @Output() outValue: EventEmitter<any> = new EventEmitter();
    inValue: any = 'take';//机构吗回显为当前登录人的所属机构
    first: number = 0;
    msgs: Message[] = [];
    orgTreePage: boolean = false;
    treeOrgName: any;
    treeOrgId: any;
    personId: string;
    personName: any = [];//员工
    personPage: boolean = false;
    parent_value: any;
    operatingLevel: string;
    ch: any = CALENDAR_CN;
    estimatedStartDt: Date = null;//
    estimatedEndDt: Date = null;//
    depositTable: any = [];
    total: number;
    StartDate: Date = null;

    user: any;
    user2: any;
    orgId: string = '';
    userId: string = '';
    userName: string = '';
    orgName: string = '';

    depositHistoryBean: DepositHistoryBean = new DepositHistoryBean();
    depositHistoryTotalBean: DepositHistoryTotalBean = new DepositHistoryTotalBean();
    downloadPerson: boolean = false;
    waitSelUserList: any;
    outVal: any;
    extParam: any;
    new_postId: any = [];
    code: any;
    which_one: any;
    cooperator: string;//协储员
    cooperatorGlobal: any = [];//
    cooperator_ID: string;//
    cooperator_IDGlobal: any = [];//
    parent_value2: string;//

    userform: FormGroup;
    loanDepositValue: string;//
    // endDate_new = new Date(new Date().getTime() - 24 * 60 * 60 * 1000);
    endDate_new = new Date();
    collection: any = [{}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}];
    flag: number = 1;//用来在查询之后清空协储员
    constructor(
        public commfunc: Commfunc,
        public httpService: CusOperationHttpService,
        public fb: FormBuilder,
    ) {
        this.user = JSON.parse(this.commfunc.getSessionData('commonHeader'));
        this.user2 = JSON.parse(this.commfunc.getSessionDataCH('chName'));
        if (this.user.orgId) {
            this.orgId = this.user.orgId;
        }
        if (this.user.userId) {
            this.userId = this.user.userId;
        }
        if (this.user2.userName) {
            this.userName = this.user2.userName;
        }
        if (this.user2.orgName) {
            this.orgName = this.user2.orgName;
        }
        if (this.user.extParam) {
            this.extParam = this.user.extParam;
        }
        if (this.extParam.managePermission == 'Y') {
            this.cooperator = '';
            this.cooperator_ID = '';
        } else {
            this.cooperator = this.userName;
            this.cooperator_ID = this.userId;
        }

        // debugger
        //默认回显当前任职机构的上一级机构,只有支行部门的回显上一级
        if (this.extParam.operatingLevel == 'SUB_BRANCH_DT') {
            this.queryUpOrg();
        } else {
            //默认回显任职机构
            let dropDownSelect = this.commfunc.getSessionDataCH('dropDownSelect');
            this.treeOrgName = dropDownSelect.split(",")[0];
            this.treeOrgId = this.extParam.orgId;
            this.parent_value = this.extParam.orgId;
            this.loanDepositValue = this.extParam.orgId;
        }
    }

    ngOnInit() {
        this.userform = this.fb.group({
            treeOrgName: ['', Validators.required],
            StartDate: ['', Validators.required],
            cooperator: [''],
            personName: [''],
            custNum: [''],
            borrowAmtStart: [''],
            borrowAmtEnd: [''],
            loanAmtStart: [''],
            loanAmtEnd: [''],
        });
        this.parent_value2 = '0000';
        this.codeValues();
        //下载判断
        this.new_postId = this.code['downPostId'];
        setTimeout(() => {
            this.depositTable = [];
        })
        this.depositHistoryBean.pageNum = 1;
        this.depositHistoryBean.pageSize = 10;
        // this.depositHistoryBean.cooperator = this.userId;

        this.StartDate = this.endDate_new;
        if (this.extParam.operatingLevel != 'SUB_BRANCH_DT') {
            this.queryFirst(1);
        }
    }
    //码值
    codeValues() {
        this.commfunc.codeValue();
        let ret = this.commfunc.codeDatas;
        return this.code = ret //code是自己声明的变量
    }
    treeCall(param) {
        debugger
        this.orgTreePage = false;
        this.treeOrgName = param.orgName;
        this.treeOrgId = param.orgId;
        this.parent_value = param.orgId;
        this.operatingLevel = param.operatingLevel;
    }
    perTreeShow() {
        this.orgTreePage = true;
    }
    perShow(data) {
        this.which_one = data;
        if (data == 1) {
            if (this.treeOrgId == '' || !this.treeOrgId) {
                this.msgs = [];
                this.msgs = [{ severity: 'error', summary: '提示', detail: '机构号为空,请选择机构' }];
                return;
            } else {
                this.personPage = true;
            }
        } else if (data == 2) {
            this.personPage = true;
        }
    }
    perInfor(item) {
        // console.log(this.flag)
        if (this.which_one == 1 && this.flag == 1) {//查询之后再次点击协储员
            this.cooperator = '';
            this.cooperatorGlobal = [];
            this.cooperator_ID = '';
            this.cooperator_IDGlobal = [];
        }
        this.flag++;
        this.personPage = false;
        if (this.which_one == 1) {
            this.cooperatorGlobal.push(item[0].tellerName);
            this.cooperator = this.cooperatorGlobal.join(',');
            this.cooperator_IDGlobal.push(item[0].tellerId);
            this.cooperator_ID = this.cooperator_IDGlobal.join(',');
        } else if (this.which_one == 2) {
            this.personName = item[0].tellerName;
            this.personId = item[0].tellerId;
        }
    }
    showStart() {
        if (this.estimatedStartDt && this.estimatedStartDt != null && this.estimatedEndDt && this.estimatedEndDt != null) {
            if (this.estimatedStartDt.getTime() > this.estimatedEndDt.getTime()) {
                this.msgs = [];
                this.msgs = [{ severity: 'error', summary: '提示', detail: '开始日期必须小于到期日期!' }];
                setTimeout(() => {
                    this.estimatedStartDt = null;
                });
                return
            }
        }
    }
    showEnd() {
        if (this.estimatedStartDt && this.estimatedStartDt != null && this.estimatedEndDt && this.estimatedEndDt != null) {
            if (this.estimatedStartDt.getTime() > this.estimatedEndDt.getTime()) {
                this.msgs = [];
                this.msgs = [{ severity: 'error', summary: '提示', detail: '到期日期必须大于开始日期!' }];
                setTimeout(() => {
                    this.estimatedEndDt = null;
                });
                return
            }
        }
    }
    //校验客户号只能输入数字
    onKeyupNumber(event) {
        if (event.target.value != '') {
            if (checkNumber(null, event.target.value)) {
                this.msgs = [{ severity: 'error', summary: '提示', detail: '当前输入框内只能输入数字!' }];
                event.target.value = '';
                return
            }
        }
    }
    checkNum(item, num) {
        if (item == 'borrowAmt') {
            if (this.depositHistoryBean.borrowAmtStart != '' && this.depositHistoryBean.borrowAmtEnd != '') {
                if (parseFloat(this.depositHistoryBean.borrowAmtStart) > parseFloat(this.depositHistoryBean.borrowAmtEnd)) {
                    this.msgs = [];
                    this.msgs.push({ severity: 'error', summary: '提示', detail: "最低借方发生额不能大于最高借方发生额!" });
                    if (num == 1) {
                        setTimeout(() => {
                            this.depositHistoryBean.borrowAmtStart = '';
                        });
                    } else if (num == 2) {
                        setTimeout(() => {
                            this.depositHistoryBean.borrowAmtEnd = '';
                        });
                    }
                    return
                }
            }
        } else if (item == 'loanAmt') {
            if (this.depositHistoryBean.loanAmtStart != '' && this.depositHistoryBean.loanAmtEnd != '') {
                if (parseFloat(this.depositHistoryBean.loanAmtStart) > parseFloat(this.depositHistoryBean.loanAmtEnd)) {
                    this.msgs = [];
                    this.msgs.push({ severity: 'error', summary: '提示', detail: "最低贷方发生额不能大于最高贷方发生额!" });
                    if (num == 1) {
                        setTimeout(() => {
                            this.depositHistoryBean.loanAmtStart = '';
                        });
                    } else if (num == 2) {
                        setTimeout(() => {
                            this.depositHistoryBean.loanAmtEnd = '';
                        });
                    }
                    return
                }
            }
        }
    }
    queryFirst(index?) {
        if (!index) {
            //校验的
            for (const i in this.userform.controls) {
                this.userform.controls[i].markAsDirty();
            }
            //处理是否提交
            for (const i in this.userform.controls) {
                if (!this.userform.controls[i].valid) {
                    return;
                }
            }

            this.flag = 1;//查询之后
        }
        this.depositHistoryBean.pageNum = 1;
        this.depositHistoryBean.pageSize = 10;
        this.first = 0;
        // this.query();
        let param = {
            orgId: this.treeOrgId,
            cooperator: this.cooperator_ID,
            counterEmp: this.personId,
            operatingLevel: this.operatingLevel,
            custNum: this.depositHistoryBean.custNum,
            borrowAmtStart: this.depositHistoryBean.borrowAmtStart,
            borrowAmtEnd: this.depositHistoryBean.borrowAmtEnd,
            loanAmtStart: this.depositHistoryBean.loanAmtStart,
            loanAmtEnd: this.depositHistoryBean.loanAmtEnd,
            transDate: this.commfunc.transDateN(this.StartDate)
        };
        this.outValue.emit(param);
    }
    query() {
        this.depositHistoryBean.orgId = this.treeOrgId;
        this.depositHistoryBean.counterEmp = this.personId;
        this.depositHistoryBean.cooperator = this.cooperator_ID;
        this.depositHistoryBean.transDate = this.commfunc.transDateN(this.StartDate);
        this.httpService.queryDepostHistoryDetails(this.depositHistoryBean).subscribe(data => {
            if (data.returnCode.code == 'success') {
                this.depositTable = data.resultList;
                this.total = data.total;
                this.depositHistoryTotalBean.borrowTotal = data.borrowTotal;
                this.depositHistoryTotalBean.borrowAmt = data.borrowAmt;
                this.depositHistoryTotalBean.loanTotal = data.loanTotal;
                this.depositHistoryTotalBean.loanAmt = data.loanAmt;
            } else {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
            }
        }, err => {
            this.msgs = [];
            this.msgs = [{ severity: 'error', summary: '提示', detail: '调用失败!' }];
        });
    }
    download() {
        //校验的
        for (const i in this.userform.controls) {
            this.userform.controls[i].markAsDirty();
        }
        //处理是否提交
        for (const i in this.userform.controls) {
            if (!this.userform.controls[i].valid) {
                return;
            }
        }

        let s = JSON.parse(this.commfunc.getSessionData('commonHeader'));
        if (s.extParam.specifyNextOpers) {
            delete s.extParam.specifyNextOpers
            let commonHeader = s;
            this.commfunc.setSessionData('commonHeader', JSON.stringify(commonHeader));
        }
        let this_id = this.extParam.postId;
        let flag = true;
        for (let item of this.new_postId) {
            if (this_id == item.value) {
                flag = false;
                break
            }
        }
        if (!flag) {
            this.toDownload(flag);
        } else {
            let lcbl: any = {
                lctybl: this.extParam.postId
            }
            this.waitSelUserList = this.commfunc.query('XZZXLC', lcbl);
            if (!this.waitSelUserList.erro) {
                this.outVal = this.waitSelUserList.waitSelUserList;
                let chooseThis = this.waitSelUserList.display;
                let Header = JSON.parse(this.commfunc.getSessionData('commonHeader'));
                let a2 = Header.extParam.specifyNextOpers;
                if (chooseThis == true && (a2 == undefined || a2 == '' || a2 == null)) {
                    this.downloadPerson = true;
                } else {
                    this.toDownload(flag);
                }
            } else {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '提示', detail: this.waitSelUserList.erro });
            }
        }
    }
    toDownload(flag) {
        this.depositHistoryBean.orgId = this.treeOrgId;
        this.depositHistoryBean.counterEmp = this.personId;
        this.depositHistoryBean.cooperator = this.cooperator_ID;
        this.depositHistoryBean.transDate = this.commfunc.transDateN(this.StartDate);
        this.httpService.depostHistoryDetailsExportFile(this.depositHistoryBean).subscribe(data => {
            if (data.returnCode.code == 'success') {
                if (flag) {
                    this.msgs = [];
                    this.msgs.push({ severity: 'success', summary: '提示', detail: "提交成功!" });
                    return
                }
                this.msgs = [];
                this.msgs.push({ severity: 'success', summary: '提示', detail: "下载成功!" });
            } else {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
            }
        }, err => {
            this.msgs = [];
            this.msgs = [{ severity: 'error', summary: '提示', detail: '调用失败!' }];
        });
    }
    outDisplayCall(event) {
        this.downloadPerson = event;
    }
    outValueCall(event) {
        this.outVal = event;
        let ss = '';
        this.outVal.forEach(item => {
            ss += item.tellerId + ','
        });
        let commonHeader = JSON.parse(this.commfunc.getSessionData('commonHeader'));
        commonHeader.extParam['specifyNextOpers'] = ss;
        this.commfunc.setSessionData('commonHeader', JSON.stringify(commonHeader));
        this.toDownload(true);
    }
    downloadHide() {
        this.downloadPerson = false;
    }
    paginate(event) {
        //每页显示的条数
        this.depositHistoryBean.pageSize = event.rows * 1;
        //当前页
        this.depositHistoryBean.pageNum = event.page + 1;
        this.first = event.page * this.depositHistoryBean.pageSize;
        this.query();
    }
    //重置
    reset() {
        this.depositHistoryBean = new DepositHistoryBean();
        this.depositHistoryBean.pageNum = 1;
        this.depositHistoryBean.pageSize = 10;

        this.personName = '';
        this.personId = '';
        this.StartDate = new Date();
        this.endDate_new = new Date();
        //客户经理、支行行长/业务部门主管，重置时不用清空机构
        if (this.extParam.operatingLevel == 'SUB_BRANCH'
            || this.extParam.operatingLevel == 'SUB_BRANCH_DT'
            || this.extParam.managePermission == 'N') {
            this.treeOrgName = this.orgName;
            this.treeOrgId = this.orgId;
        } else {
            this.treeOrgName = '';
            this.treeOrgId = '';
        }
        if (this.extParam.managePermission != 'N') {
            this.cooperator = '';
            this.cooperatorGlobal = [];
            this.cooperator_ID = '';
            this.cooperator_IDGlobal = [];
        }
    }
    //查询上一级机构
    queryUpOrg() {
        let param = {
            flag: "0",
            orgId: this.extParam.orgId,
            rowNum: "10",
            startRow: "1",
        }
        this.httpService.queryOrgByCombConditions(param).subscribe(data => {
            if (data.returnCode.code == 'success') {
                if (data.orgInfoList && data.orgInfoList.length > 0) {
                    this.treeOrgName = data.orgInfoList[0].superiorManageOrgName;
                    this.treeOrgId = data.orgInfoList[0].superiorManageOrg;
                    this.parent_value = data.orgInfoList[0].superiorManageOrg;
                    this.loanDepositValue = data.orgInfoList[0].superiorManageOrg;
                    this.queryFirst(1);
                }
            } else {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
            }
        }, err => {
            this.msgs = [];
            this.msgs = [{ severity: 'error', summary: '提示', detail: '调用失败!' }];
        });
    }
    //权限
    judgeField(id) {
        return this.commfunc.permissionCheck(id)
    }
}
