import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';
import { TreeNode } from 'primeng/primeng';
import { Message } from 'primeng/primeng';
// import { SelectProductBean } from './bean/select-product.bean';

import { ActivatedRoute } from '@angular/router';
import { tableMessage } from 'app/@uisftech/common/constant/config';
import * as _ from 'lodash';
import { CALENDAR_CN } from 'app/@uisftech/common/constant/config';
import { DealHttpService } from 'app/pages/tzb/order/http/deal.http.service';
import { Commfunc } from 'app/@uisftech/common/service/commonFun';
import { UisftechCommonHttpService } from 'app/@uisftech/common/http/uisftech-common-http.service';
import { LoanHistoryBean } from '../bean/loanHistory.bean';
@Component({
    selector: 'bussType-tree',
    templateUrl: './bussType-tree.component.html',
    styleUrls: ['./bussType-tree.component.scss'],
    providers: [Commfunc, UisftechCommonHttpService]
})
export class BussTypeTreeComponent implements OnInit {
    @Output() outValue: EventEmitter<any> = new EventEmitter();

    head_title: string = '业务目录详情';
    //提示信息
    msgs: Message[] = [];
    selectedFiles;
    ch: any = CALENDAR_CN;
    //公共
    tableMesg: any = tableMessage;

    //列表
    cars_2: any = [];
    treeData: any;
    left_tree: any = [];
    left_tree_arr: any = [];
    hierarchy_arr: any = [];

    all_tree; any = [];

    first_id: string;//产品码值    临时固定CATALOG00000003
    current_id: string; //当前选中的目录id
    s_productId: string;//搜索条件
    s_productName: string;//搜索条件
    //查询条件
    productId: string = '';//产品id
    productName: string = '';//产品名称
    //分页
    totalNum: any;//总页数
    pageSize: number = 10;//每页多少条
    pageNum: number = 1;//当前页

    first: number = 0;

    //bean
    loanHistoryBean: LoanHistoryBean = new LoanHistoryBean();
    //获取所有码值
    code: any;
    //选择数据
    chooseData: any = [];
    checkAll: boolean = false;
    ctlgIdV: string = '';
    maxIdLevelV: number;
    ctlgIdLevelV: number;
    ctlgOrCategoryLocalCode: string;//自定义分类编号
    constructor(
        public uisftechCommonHttpService: UisftechCommonHttpService,
        public commfunc: Commfunc,
        public router: ActivatedRoute,
    ) {
        setTimeout(() => {
            this.cars_2 = [];
        })

    }

    ngOnInit() {

        this.current_id = this.first_id;//初始化默认  第一级
        this.queryCatalog()
    }

    //码值
    codeValues() {
        this.commfunc.codeValue();
        let ret = this.commfunc.codeDatas;
        return this.code = ret //code是自己声明的变量
    }

    //调用公共参数项服务查询业务目录编码
    queryCatalog() {
        let param = { paramGroupId: "COP", paramName: "loanHistoryDetails" }

        this.uisftechCommonHttpService.queryCommonParamItemService(param).subscribe(data => {
            if (data.returnCode.code == "success") {
                if (data.returnCommonParamItemList && data.returnCommonParamItemList.length > 0) {
                    this.first_id = data.returnCommonParamItemList[0].paramItemName;
                    this.current_id = this.first_id;//初始化默认  第一级
                    this.query_line();
                }
            } else {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '提示', detail: data.errorMessage });
            }
        }, error => {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败！' });
        })

    }





    query_line() {
        let parm = { ctlgId: this.first_id }
        this.uisftechCommonHttpService.ctlgMenuQuery(parm).subscribe(data => {
            if (data.returnCode.code == "success") {
                this.all_tree = data.customerMenuList;
                for (let items of this.all_tree) {
                    this.hierarchy_arr = this.getMeuInfo(this.all_tree, items.id)
                    let parames = {
                        ctlgId: items.id,
                        maxIdLevel: this.hierarchy_arr[1],
                        ctlgIdLevel: this.hierarchy_arr[0] - 1,
                    }
                    let delData = (data) => {
                        this.ctlgOrCategoryLocalCode = data.departmentList.departmentList[0].basicMessage.ctlgOrCategoryLocalCode;
                        items['ctlgOrCategoryLocalCode'] = data.departmentList.departmentList[0].basicMessage.ctlgOrCategoryLocalCode;
                    }
                    this.commfunc.interfaceComFnc(this.uisftechCommonHttpService.ctlgManageContactAttrInfo(parames), delData, this.msgs);
                }
                this.left_tree_arr = this.hierarchyTree(this.all_tree, '', '');
                console.log(this.left_tree_arr)
            } else {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '提示', detail: data.errorMessage });
            }
        }, error => {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败！' });
        })
    }

    // -  号点击
    nodeCheck(event) {
        console.log(event)
        this.current_id = event.node.value[0].productId;

        let arr = [];
        for (let item of this.all_tree) {
            if (event.node.value[0].productId == item.pid) {
                arr.push({ productName: item.name, productId: item.ctlgOrCategoryLocalCode });
            }
        }
        if (arr.length == 0) {
            arr.push({ productName: event.node.label, productId: event.node.value[0].ctlgOrCategoryLocalCode });
        }

        this.cars_2 = arr;
    }
    //+号点击
    nodeExpend(event) {
        event.node.children = this.hierarchyTree(this.all_tree, event.node.value[0].productId, event.node.label);

    }

    //层级树
    hierarchyTree(menu_list, pid, categoryName) {
        let menu_arr = [];
        for (let key of menu_list) {
            if (key.pid === pid) {
                menu_arr.push({
                    // children: [{}],
                    label: key.name,
                    value: [{
                        productId: key.id,
                        productName: key.name,
                        productCategoryId: key.pid,
                        categoryName: categoryName,
                        ctlgOrCategoryLocalCode: key.ctlgOrCategoryLocalCode
                    }]
                });
            }
        }
        return menu_arr;
    }


    //层级树
    hierarchyTree2(menu_list, pid) {
        let menu_arr = [];
        for (let key of menu_list) {
            if (key.pid === pid) {
                menu_arr.push({
                    productName: key.name,
                    productId: key.id
                });
            }
        }
        return menu_arr;
    }

    hierarchyTree1(menu_list, pid) {
        let menu_arr = [];
        for (let key of menu_list) {
            //  if(key.pid === pid){
            menu_arr.push({
                children: [{}],
                label: key.catalogName,
                value: [key.prodCatalogId]
            });
            //  }
        }
        return menu_arr;
    }

    getMeuInfo(tree, curren_pid) {//一级菜单  主目录id
        let count = 0;
        let getFirstId = (tree) => {
            let id = '';
            tree.every(item => {
                item.prodCatalogId == '' ? id = item.prodCatalogId : null;
            });
            return id;
        }
        //末级菜单id
        let getLastId = (tree) => {
            let id_arr = [];
            tree.forEach(item => {
                let stu = tree.every(item2 => {
                    if (item.prodCatalogId != item2.pid) {
                        return true;
                    }
                }) ? id_arr.push(item.id) : null;
            });
            return id_arr;
        }
        //最大层级深度 
        let getMaxHierarchy = (tree, first_id, last_arr) => {
            let arr = [];
            last_arr.forEach(item => {
                count = 0;
                let max = currentHierarchy(tree, first_id, item)
                arr.push(max);
            });
            return arr.sort()[arr.length - 1];
        }
        //当前层级深度
        //记录层级次数
        function currentHierarchy(tree, first_id, pid) {
            if (pid != first_id) {
                tree.some(item => {
                    if (item.id === pid) {
                        count++;
                        currentHierarchy(tree, first_id, item.pid);
                        return false;
                    }
                });

            }
            return count;
        }

        let first_id = getFirstId(tree);//一级菜单id
        let last_arr = getLastId(tree);//末级菜单 id组
        let current_hierarchy = currentHierarchy(tree, first_id, curren_pid);//当前层级
        let max_hierarchy = getMaxHierarchy(tree, first_id, last_arr);//最大层级 
        return [current_hierarchy, max_hierarchy]
    }



    //根据产品id、产品名称查列表点击事件
    queryProListClick() {
        this.chooseData = [];
        this.pageNum = 1;
        this.pageSize = 10;
        this.queryProList();
    }
    //根据产品id、产品名称查列表查询服务
    queryProList() {
        let params = {
            ctlgId: this.current_id,
            productId: this.s_productId,
            productName: this.s_productName,
            pageNum: this.pageNum,
            pageSize: this.pageSize
        }
        this.uisftechCommonHttpService.toCustomerOperation(params).subscribe(data => {
            if (data.returnCode.code == "success") {
                this.cars_2 = data.customerListtemp;
                this.totalNum = data.total;
            } else {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '提示', detail: data.errorMessage });
            }
        }, error => {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败！' });
        })
    }

    //分页
    paginate(event, param) {
        if (param == 'proQuery') {
            //每页显示的条数
            this.pageSize = event.rows * 1;
            //当前页
            let currentPage = event.page + 1;
            this.pageNum = currentPage;
            this.first = event.page * event.rows;
            //调用查询的方法
            this.queryProList();
        }
    }

    //重置按钮
    resetClick() {
        this.s_productId = '';//清空产品id
        this.s_productName = '';//清空产品名称
    }



    //确定按钮事件
    addBtnCom() {
        if (this.chooseData && this.chooseData.length > 0) {
            this.outValue.emit(this.chooseData);
        } else {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '提示', detail: '请选择产品' });
            return
        }
    }

    //贷款业务全选按钮
    checkAllClick() {
        this.chooseData = [];
        for (let i = 0; i < this.cars_2.length; i++) {
            if (this.checkAll == true) {
                this.cars_2[i].checkBox = true;
                this.chooseData.push(this.cars_2[i]);
            }
            else {
                this.chooseData = [];
                this.cars_2[i].checkBox = false;
            }
        }
    }
    //贷款业务单选按钮
    checkBoxClick(i, data) {
        if (data.checkBox) {
            this.chooseData.push(data)
        } else {
            this.chooseData.forEach((item, i) => {
                if (item == data) {
                    this.chooseData.splice(i, 1)
                }
            });
        }
        if (this.chooseData.length == this.cars_2.length) {
            this.checkAll = true;
        } else {
            this.checkAll = false;
        }
    }
    //查询自定义分类编号
    productQuery() {
        let parames = {
            pageSize: this.pageSize,
            pageNum: this.pageNum,
            ctlgId: this.ctlgIdV,
            maxIdLevel: this.maxIdLevelV,
            ctlgIdLevel: this.ctlgIdLevelV
        }
        let delData = (data) => {
            this.ctlgOrCategoryLocalCode = data.departmentList.departmentList[0].basicMessage.ctlgOrCategoryLocalCode
        }
        this.interfaceComFnc(this.uisftechCommonHttpService.ctlgManageContactAttrInfo(parames), delData);
    }
    interfaceComFnc(service_method, success_fnc) {
        service_method.subscribe(data => {
            if (data.returnCode != null && data.returnCode.code == "success") {
                if (success_fnc !== null) {
                    success_fnc(data);
                }
            } else {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
            }
        },
            error => {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败！' });
            });
    }
}

